import { createAction, handleActions } from 'redux-actions';
import { currentDate, formatRef, MONTH_YEAR_PATTERN, refToFormat } from 'summer';
import { GlobalParameter } from '../../models/dtos/ger/globalParameter';

export interface GlobalState {
  globalParameter: GlobalParameter;
  currentState: string;
  jaNotificouMensageria: boolean;
  sidebarToggle: boolean;
  sidebarToggleMobile: boolean;
  headerSearchHover: boolean;
  headerDrawerToggle: boolean;
}

const initialState: GlobalState = {
  currentState: 'GER',
  globalParameter: null,
  headerDrawerToggle: false,
  headerSearchHover: false,
  jaNotificouMensageria: false,
  sidebarToggle: false,
  sidebarToggleMobile: false,
};

function getReferenciaFromModule(refAtual: string) {
  let result = refToFormat(refAtual);
  if (result) {
    result = `Referncia Atual: ${result}`;
  } else {
    result = 'Sem referncia';
  }
  return result;
}

export const referenciaFiscal = (globalParameter: GlobalParameter, currentState: string): string => {
  let result: string = 'Sem referncia';
  if (globalParameter && currentState) {
    switch (currentState) {
      case 'MCG':
        if (globalParameter.empresaMCG) {
          result = getReferenciaFromModule(globalParameter.empresaMCG.refAtual);
        }
        break;
      case 'MCP':
        if (globalParameter.empresaMCP) {
          result = getReferenciaFromModule(globalParameter.empresaMCP.refPatrimonial);
        }
        break;
      case 'MFP':
        if (globalParameter.empresaMFP) {
          result = getReferenciaFromModule(globalParameter.empresaMFP.referencia);
        }
        break;
      case 'MLF':
        if (globalParameter.empresaMLF) {
          result = getReferenciaFromModule(globalParameter.empresaMLF.referenciaAtual);
        }
        break;
      default:
        result = `Referncia Atual: ${formatRef(currentDate(), MONTH_YEAR_PATTERN)}`;
        break;
    }
  }
  return result;
};

export const setGlobalParameter = createAction('SET_GLOBAL_PARAMETER');
export const setCurrentState = createAction('SET_CURRENT_STATE');
export const setNotificarMensageria = createAction('SET_NOTIFICAR_MENSAGERIA');
export const toggleSidebar = createAction('TOGGLE_SIDEBAR');
export const toggleMobileSidebar = createAction('TOGGLE_MOBILE_SIDEBAR');
export const toggleHeaderSearch = createAction('TOGGLE_HEADER_SEARCH');
export const toggleHeaderDrawer = createAction('TOGGLE_HEADER_DRAWER');

export const reducer = handleActions<GlobalState>(
  {
    [setGlobalParameter.toString()]: (state: GlobalState, action: any) => ({
      ...state,
      globalParameter: action.payload,
    }),
    [setCurrentState.toString()]: (state: GlobalState, action: any) => ({
      ...state,
      currentState: action.payload,
    }),
    [setNotificarMensageria.toString()]: (state: GlobalState, action: any) => ({
      ...state,
      jaNotificouMensageria: action.payload,
    }),
    [toggleSidebar.toString()]: (state: GlobalState) => ({
      ...state,
      sidebarToggle: !state.sidebarToggle,
      sidebarToggleMobile: false,
    }),
    [toggleMobileSidebar.toString()]: (state: GlobalState) => ({
      ...state,
      sidebarToggle: false,
      sidebarToggleMobile: !state.sidebarToggleMobile,
    }),
    [toggleHeaderSearch.toString()]: (state: GlobalState) => ({
      ...state,
      headerSearchHover: !state.headerSearchHover,
    }),
    [toggleHeaderDrawer.toString()]: (state: GlobalState) => ({
      ...state,
      headerDrawerToggle: !state.headerDrawerToggle,
    }),
  },
  initialState
);
