import { AnimatePresence, motion } from 'framer-motion';
import React, { FC, Suspense, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, Route, Switch, useHistory, useLocation } from 'react-router-dom';
import { createLoadable, dockActions, gaPageView, initializeGA, LoginState, SuspenseLoading } from 'summer';
import { Reducers } from './store/ducks';
import { actions as multiPageActions } from './store/ducks/multipage.duck';
import MultiPage from './views/components/multiPage/MultiPage';
import Sidebar from './views/components/sidebar/Sidebar';
import gerRoutes from './views/pages/ger/gerRoutes';
import mcgRoutes from './views/pages/mcg/mcgRoutes';
import mcpRoutes from './views/pages/mcp/mcpRoutes';
import mfpRoutes from './views/pages/mfp/mfpRoutes';
import mhoRoutes from './views/pages/mho/mhoRoutes';
import mlfRoutes from './views/pages/mlf/mlfRoutes';

const LoginForm = createLoadable(() => import('./views/pages/login/LoginForm'), 'LoginForm');
const pages = [...gerRoutes, ...mcgRoutes, ...mcpRoutes, ...mfpRoutes, ...mhoRoutes, ...mlfRoutes];
const allPaths = pages.map(it => it.path);

const pageVariants = {
  in: {
    opacity: 1,
    scale: 1,
  },
  initial: {
    opacity: 0,
    scale: 0.99,
  },
  out: {
    opacity: 0,
    scale: 1.01,
  },
};

const pageTransition = {
  duration: 0.4,
  ease: 'anticipate',
  type: 'tween',
};

const Routes: FC = () => {
  const loginReducer = useSelector<Reducers, LoginState>(state => state.loginReducer);
  const location = useLocation();
  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(
    () => {
      initializeGA('UA-159160015-1');
      const unlisten = gaPageView(history);

      return () => unlisten();
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(() => {
    dispatch(dockActions.destroyDock());
  }, [dispatch, location]);

  useEffect(() => {
    dispatch(multiPageActions.clearPage());
  }, [dispatch, location.pathname]);

  return (
    <AnimatePresence>
      <Suspense fallback={<SuspenseLoading />}>
        <Switch>
          <Route exact={true} path={['/']}>
            <Switch location={location} key={location.pathname}>
              <motion.div initial="initial" animate="in" exit="out" variants={pageVariants} transition={pageTransition}>
                <Route exact={true} path="/" component={LoginForm} />
              </motion.div>
            </Switch>
          </Route>

          <Route path={allPaths}>
            <Sidebar>
              <Switch location={location} key={location.pathname}>
                <motion.div initial="initial" animate="in" exit="out" variants={pageVariants} transition={pageTransition}>
                  <MultiPage>
                    {pages.map(({ component, ...routeProps }, i) => {
                      let result;
                      if (loginReducer.param) {
                        result = <Route key={i} component={component} {...routeProps} />;
                      } else {
                        result = <Redirect key={i} to="/" />;
                      }
                      return result;
                    })}
                  </MultiPage>
                </motion.div>
              </Switch>
            </Sidebar>
          </Route>
        </Switch>
      </Suspense>
    </AnimatePresence>
  );
};

export default Routes;
