import cx from 'clsx';
import React, { FC, useState } from 'react';
import { ChevronRight } from 'react-feather';
import { NavLink } from 'react-router-dom';
import { Collapse } from 'reactstrap';
import { generateTestId } from 'summer';
import { MenuItem } from './SidebarNav';

interface SidebarNavItemProps {
  item: MenuItem;
}

const SidebarNavItem: FC<SidebarNavItemProps> = ({ item }) => {
  const [itemOpen, setItemOpen] = useState(false);

  function toggleItem(event) {
    event.preventDefault();
    setItemOpen(!itemOpen);
  }

  function getItemIcon() {
    return item.icon ? <span className={cx('sidebar-icon', item.icon)} /> : null;
  }

  function generateTestIdFromValue(val: string) {
    return generateTestId(
      val
        .toLowerCase()
        .normalize('NFD')
        .replace(/^\//g, '')
        .replace(/\//g, '-')
        .replace(/ /g, '')
        .replace(/[\u0300-\u036f]/g, '')
    );
  }

  function navDropdown() {
    return (
      <li>
        <a href="/" onClick={toggleItem} className={cx({ active: itemOpen })} {...generateTestIdFromValue(item.label)}>
          {getItemIcon()}
          <span className="sidebar-item-label">{item.label}</span>
          <span className="sidebar-icon-indicator">
            <ChevronRight />
          </span>
        </a>
        <Collapse isOpen={itemOpen}>
          <ul>
            {item.content.map((it, index) => (
              <SidebarNavItem key={index} item={it} />
            ))}
          </ul>
        </Collapse>
      </li>
    );
  }

  function navLink() {
    const url = item.to || '';
    const itemIcon = getItemIcon();
    const dataTest = generateTestIdFromValue(url || item.label);
    return (
      <li>
        {isExternal(url) ? (
          <a href={url} target="_blank" rel="noopener noreferrer" {...dataTest}>
            {itemIcon}
            {item.label}
          </a>
        ) : (
          <NavLink to={url} {...dataTest}>
            {itemIcon}
            {item.label}
          </NavLink>
        )}
      </li>
    );
  }

  function isExternal(url: string) {
    const link = url ? url.substring(0, 4) : '';
    return link === 'http';
  }

  return item.content ? navDropdown() : navLink();
};

export default SidebarNavItem;
