import { AxiosInstance, AxiosResponse } from 'axios';
import { AxiosParam, create, send } from 'summer';
import { PrinterDataBasic } from '../models/dtos/ger/printerDataBasic';
import { ReportBasicValues } from '../models/dtos/ger/reportBasicValues';

function getService(addGzip: boolean = true): AxiosInstance {
  return create('/report', addGzip);
}

function getOldService(addGzip: boolean = true): AxiosInstance {
  return create('/old-report', addGzip);
}

export function download(uid: string, param: AxiosParam<void>) {
  send(getService().get<void>(`/download/${uid}`), param);
}

export function fileDownload(uid: string, param: AxiosParam<AxiosResponse>) {
  getService(false)
    .get<void>(`/fileDownload${uid}`, { responseType: 'blob' })
    .then((response: AxiosResponse) => {
      param.thenFunction(response);
    });
}

export function gerarReport(parameter: ReportBasicValues, reportName: string, param: AxiosParam<string>) {
  send(getService(false).post<string>(`/${reportName}`, parameter), param);
}

export function gerarOldReport(parameter: PrinterDataBasic, param: AxiosParam<string>) {
  send(getOldService(false).post<string>('/printRel', parameter), param);
}

export function downloadOldReport(param: AxiosParam<AxiosResponse>) {
  getOldService(false)
    .get<void>('/download-old', { responseType: 'blob' })
    .then((response: AxiosResponse) => {
      param.thenFunction(response);
    });
}
