import React, { FC, useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { useSelector } from 'react-redux';
import { Reducers } from '../../../store/ducks';
import { GlobalState } from '../../../store/ducks/global.duck';
import gerMenu from '../../pages/ger/gerMenu';
import mcgMenu from '../../pages/mcg/mcgMenu';
import mcpMenu from '../../pages/mcp/mcpMenu';
import mfpMenu from '../../pages/mfp/mfpMenu';
import mhoMenu from '../../pages/mho/mhoMenu';
import mlfMenu from '../../pages/mlf/mlfMenu';
import SidebarNav from './SidebarNav';

const SidebarMenu: FC = () => {
  const { currentState } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [currentMenu, setCurrentMenu] = useState([]);
  const [label, setLabel] = useState('');

  useEffect(() => {
    switch (currentState) {
      case 'GER':
        setCurrentMenu(gerMenu);
        setLabel('Mdulo Principal');
        break;
      case 'MCG':
        setCurrentMenu(mcgMenu);
        setLabel('Mdulo Contbil');
        break;
      case 'MCP':
        setCurrentMenu(mcpMenu);
        setLabel('Mdulo Controle Patrimonial');
        break;
      case 'MFP':
        setCurrentMenu(mfpMenu);
        setLabel('Mdulo Folha de Pagamento');
        break;
      case 'MHO':
        setCurrentMenu(mhoMenu);
        setLabel('Mdulo de Honorrios');
        break;
      case 'MLF':
        setCurrentMenu(mlfMenu);
        setLabel('Mdulo Fiscal');
        break;
      default:
    }
  }, [currentState]);

  return (
    <PerfectScrollbar>
      <div className="sidebar-navigation">
        <div className="sidebar-header">
          <span>{label}</span>
        </div>
        <SidebarNav items={currentMenu} />
      </div>
    </PerfectScrollbar>
  );
};

export default SidebarMenu;
