import { Client } from '@stomp/stompjs';
import { environment } from 'summer';

let client: Client;
const baseURL = environment.siteURL.replace('http', 'ws').replace('wss', 'ws');

export function connect() {
  if (client && client.active) {
    client.deactivate();
  }
  client = new Client({ brokerURL: baseURL + 'spring/calima-ws' });
  return client;
}

export function disconnect() {
  if (client && client.active && client.connected) {
    client.deactivate();
  }
}
