import cx from 'clsx';
import { saveAs } from 'file-saver';
import React, { FC, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { currentDate, ERROR, Footer, INFORMATION, showToast, WARNING } from 'summer';
import { Notificacao } from '../../../models/dtos/ger/notificacao';
import { findAllJobs } from '../../../services/ger.service';
import { fileDownload } from '../../../services/report.service';
import { Reducers } from '../../../store/ducks';
import { GlobalState } from '../../../store/ducks/global.duck';
import { setJobs } from '../../../store/ducks/jobs.duck';
import { setNotifications } from '../../../store/ducks/notifications.duck';
import Header from '../header/Header';
import './sidebar.css';
import SidebarHeader from './SidebarHeader';
import SidebarMenu from './SidebarMenu';
import { connect, disconnect } from './websocket';

const download = urlArquivo => {
  fileDownload(urlArquivo, {
    errorFunction: (mensagem: string) => {
      showToast(mensagem, ERROR);
    },
    thenFunction: (response: any) => {
      const filename = response.headers['content-disposition'].split('filename=')[1];
      saveAs(response.data, filename);
    },
  });
};

const dispatchNotificationAction = (notification: Notificacao): void => {
  switch (notification.tipo) {
    case 'reportDownload':
      if (notification.paramMap) {
        showToast('O download do seu relatrio acontecer em instantes...', INFORMATION);
        download(notification.paramMap.reportFile);
      } else {
        showToast(notification.mensagem, INFORMATION);
      }
      break;
    case 'arquivoDigitalDownload':
      if (notification.paramMap && notification.paramMap.nomePadraoArquivo) {
        if (notification.paramMap.urlArquivo) {
          download(notification.paramMap.urlArquivo);
        }
        if (notification.paramMap.urlArquivoAdvertencia) {
          download(notification.paramMap.urlArquivoAdvertencia);
        }
      }
      break;
    // Outros eventos disparados
    case 'mensagensNotification':
    case 'empresaFiscalPendente':
    case 'feriasPendente':
    case 'convocacaoIntermitentePendente':
    case 'avisoPrevioTrabalhado':
    case 'examesPendentes':
    case 'contratoExperienciaPendente':
    case 'solicitacao':
    case 'solicitacaoBox':
    case 'relatorioPendencias':
    case 'relatorioPendenciasReinf':
      break;
    default:
      break;
  }
};

const Sidebar: FC = ({ children }) => {
  const { sidebarToggle, sidebarToggleMobile, headerSearchHover, headerDrawerToggle, globalParameter } = useSelector<Reducers, GlobalState>(
    state => state.globalReducer
  );
  const dispatch = useDispatch();

  useEffect(
    () => {
      let isFinanceiroUser = false;
      let isConnectUser = false;
      let isBoxUser = false;
      let idUser = 0;

      const usuario = globalParameter && globalParameter.usuario ? globalParameter.usuario : null;
      if (usuario && usuario.perfil && usuario.perfil.perfilModulo) {
        isFinanceiroUser = usuario.administrador || usuario.perfil.acessoFinanceiro;
        isConnectUser = usuario.perfil.perfilModulo.connect;
        isBoxUser = usuario.perfil.perfilModulo.box;
        idUser = usuario.id;
      }

      const client = connect();

      client.onConnect = () => {
        client.subscribe('/topic/notifications', message => {
          const novasNotificacoes = JSON.parse(message.body);
          if (Array.isArray(novasNotificacoes)) {
            const notificationType = message.headers.type;

            const isGeneralAlerts = notificationType === 'generalAlerts';
            const isFinanceiroAlerts = notificationType === 'financeiroAlerts';
            const isConnectAlerts = notificationType === 'connectAlerts';
            const isBoxAlerts = notificationType === 'boxAlerts';
            const isMensagemAlerts = notificationType === 'mensagensNotification';
            const isDownloadAlerts = notificationType === 'reportDownload' || notificationType === 'arquivoDigitalDownload';

            const isFinanceiro = isFinanceiroAlerts && isFinanceiroUser;
            const isConnect = isConnectAlerts && isConnectUser;
            const isBox = isBoxAlerts && isBoxUser;

            const isEspecialValido = isFinanceiro || isConnect || isBox;
            const alertEspecial = isFinanceiroAlerts || isConnectAlerts || isBoxAlerts;

            novasNotificacoes.forEach((novaNotificacao: Notificacao) => {
              const isUserValido = !novaNotificacao.idUsuario || novaNotificacao.idUsuario === idUser;

              if ((isEspecialValido || !alertEspecial) && isUserValido) {
                const isInformation = novaNotificacao.status === 'information';
                const isWarning = novaNotificacao.status === 'warning';
                const isError = novaNotificacao.status === 'error';
                if (isGeneralAlerts) {
                  if (isInformation) {
                    showToast(novaNotificacao.mensagem, INFORMATION);
                  } else {
                    showToast('Ocorreu um erro ao realizar o processo.', ERROR);
                  }
                }

                if ((isDownloadAlerts && isInformation) || isMensagemAlerts) {
                  dispatchNotificationAction(novaNotificacao);
                } else {
                  if (isFinanceiro) {
                    let status = INFORMATION;
                    if (isWarning) {
                      status = WARNING;
                    } else if (isError) {
                      status = ERROR;
                    }
                    showToast(
                      <>
                        <span>{novaNotificacao.mensagem}</span>
                        <br />
                        <br />
                        <a
                          style={{ textDecoration: 'underline', color: '#FFF' }}
                          href={novaNotificacao.link}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          Clique aqui para acessar!
                        </a>
                      </>,
                      { ...status, delay: false }
                    );
                  }
                }
              }
            });
            dispatch(setNotifications({ novasNotificacoes, notificationType }));
          }
        });

        client.subscribe('/topic/jobs', message => {
          findAllJobs({ errorFunction: mensagem => showToast(mensagem, ERROR), thenFunction: result => dispatch(setJobs(result)) });
        });
      };

      client.activate();

      return () => {
        disconnect();
      };
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const firstContent = (
    <>
      <span>Calima ERP Contbil</span> &copy; {currentDate().getFullYear()}
    </>
  );

  const secondContent = (
    <>
      <span>
        Desenvolvido por{' '}
        <a href="https://www.projetusti.com.br/" target="_blank" rel="noopener noreferrer">
          {' '}
          Projetus TI
        </a>
      </span>
    </>
  );

  return (
    <>
      <div
        className={cx('app-wrapper app-sidebar-fixed app-header-fixed', {
          'app-sidebar-collapsed': sidebarToggle,
          'app-sidebar-mobile-open': sidebarToggleMobile,
          'header-drawer-open': headerDrawerToggle,
          'search-wrapper-open': headerSearchHover,
        })}
      >
        <div className="app-sidebar app-sidebar--dark app-sidebar--shadow">
          <SidebarHeader />
          <div className="app-sidebar--content">
            <SidebarMenu />
          </div>
        </div>
        <div className="app-main">
          <Header />
          <div className="app-content">
            <div className="app-content--inner">
              <div className="app-content--inner__wrapper">{children}</div>
            </div>
            <Footer footerShadow={true} firstChildren={firstContent} secondChildren={secondContent} />
          </div>
        </div>
      </div>

      {/*<MensageriaView />*/}
    </>
  );
};

export default Sidebar;
