import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import cx from 'clsx';
import React, { FC, useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { useDispatch, useSelector } from 'react-redux';
import { Badge, Button, Nav, NavItem, NavLink, TabContent, TabPane, UncontrolledPopover } from 'reactstrap';
import { currentDate, formatTime } from 'summer';
import { Notificacao } from '../../../models/dtos/ger/notificacao';
import { marcarNotificacaoVista } from '../../../services/ger.service';
import { Reducers } from '../../../store/ducks';
import { NotificationState, removeNotification } from '../../../store/ducks/notifications.duck';

const timePattern = 'HH:mm';

interface GlobaNotification {
  process: Notificacao[];
  messages: Notificacao[];
  tickets: Notificacao[];
  total: number;
}

const HeaderNotifications: FC = () => {
  const { data } = useSelector<Reducers, NotificationState>(state => state.notificationReducer);
  const [globalNotification, setGlobalNotification] = useState<GlobaNotification>({ process: [], messages: [], tickets: [], total: 0 });
  const [activeTab, setActiveTab] = useState(1);
  const dispatch = useDispatch();

  useEffect(() => {
    const process: Notificacao[] = [];
    const messages: Notificacao[] = [];
    const tickets: Notificacao[] = [];

    data.forEach(item => {
      if (item.tipo === 'ticket') {
        tickets.push(item);
      } else if (item.tipo === 'mensagemController') {
        messages.push(item);
      } else {
        if (item.tipo !== 'mensagensNotification') {
          process.push(item);
        }
      }
    });

    setGlobalNotification({
      messages,
      process,
      tickets,
      total: process.length + messages.length + tickets.length,
    });
  }, [data]);

  const sortNotifications = (notificationA: Notificacao, notificationB: Notificacao) => {
    let comparison = 0;
    if (notificationA.data > notificationB.data) {
      comparison = 1;
    } else if (notificationA.data < notificationB.data) {
      comparison = -1;
    }
    return comparison * -1;
  };

  const toggleTab = (tab: number) => {
    return () => {
      if (activeTab !== tab) {
        setActiveTab(tab);
      }
    };
  };

  function getTotalLabel() {
    let label;
    if (globalNotification.total === 0) {
      label = 'Voc no possui nenhuma notificao';
    } else if (globalNotification.total === 1) {
      label = (
        <>
          Voc tem <b className="text-success">{globalNotification.total}</b> notificao
        </>
      );
    } else {
      label = (
        <>
          Voc tem <b className="text-success">{globalNotification.total}</b> notificaes
        </>
      );
    }

    return <p className="opacity-8 mb-0">{label}</p>;
  }

  const closeNotification = (notificacao: Notificacao) => {
    return () => {
      marcarNotificacaoVista(notificacao, { thenFunction: result => dispatch(removeNotification({ codigo: notificacao.codigo })) });
    };
  };

  function getStatusBadge(notificacao: Notificacao) {
    let label;
    switch (notificacao.status) {
      case 'information':
        label = <FontAwesomeIcon icon="info-circle" size="2x" className="text-first" />;
        break;
      case 'warning':
        label = <FontAwesomeIcon icon="exclamation-circle" size="2x" className="text-warning" />;
        break;
      case 'error':
        label = <FontAwesomeIcon icon="times-circle" size="2x" className="text-danger" />;
        break;
      default:
        label = <FontAwesomeIcon icon="ball-pile" size="2x" className="text-primary" />;
    }

    return (
      <div className="w-100 d-flex align-items-center justify-content-between">
        {label}
        <span onClick={closeNotification(notificacao)} className="mr-4" style={{ cursor: 'pointer' }}>
          <FontAwesomeIcon icon={['far', 'times-circle']} size="lg" className="text-dark" />
        </span>
      </div>
    );
  }

  function renderPaneItem(list: Notificacao[]) {
    list.sort(sortNotifications);
    let items = [];
    if (list && list.length > 0) {
      items = list.map(item => {
        let hour = formatTime(currentDate(), timePattern);

        if (item.hora) {
          const splitedHour = item.hora.split(':');
          hour = `${splitedHour[0]}:${splitedHour[1]}`;
        }

        return (
          <div className="timeline-item" key={item.codigo}>
            <div className="timeline-item-offset">{hour}</div>
            <div className="timeline-item--content">
              <div className="timeline-item--icon" />
              <h4 className="timeline-item--label mb-2 font-weight-bold">{getStatusBadge(item)}</h4>
              <p>{item.mensagem}</p>
            </div>
          </div>
        );
      });
    } else {
      items.push(
        <div className="timeline-item" key="no-notification">
          <div className="timeline-item-offset">{formatTime(currentDate(), timePattern)}</div>
          <div className="timeline-item--content">
            <div className="timeline-item--icon" />
            <h4 className="timeline-item--label mb-2 font-weight-bold">Nenhuma notificao</h4>
          </div>
        </div>
      );
    }

    return (
      <div className="scroll-area scroll-area-xl shadow-overflow">
        <PerfectScrollbar option={{ wheelPropagation: false, maxScrollbarLength: 800 }}>
          <div className="timeline-list timeline-list-offset timeline-list-offset-dot">{items}</div>
        </PerfectScrollbar>
      </div>
    );
  }

  return (
    <>
      <Button
        id="alertsPopover"
        color="neutral-success"
        className="bg-neutral-success text-success font-size-lg p-0 d-inline-block shadow-none border-0 text-center d-44 rounded position-relative btn-transition-none"
      >
        <span>
          {globalNotification.total > 0 && (
            <Badge color="success" className="badge-circle">
              Notificaes
            </Badge>
          )}
          <FontAwesomeIcon icon={['far', 'bell']} size="lg" />
        </span>
      </Button>
      <UncontrolledPopover
        target="alertsPopover"
        trigger="legacy"
        container="body"
        className="popover-custom-wrapper popover-custom-xl popover-notification"
        placement="auto"
      >
        <div className="bg-composed-wrapper bg-midnight-bloom mx-3 mt-3 border-0 text-center rounded-sm">
          <div className="bg-composed-wrapper--content text-light px-2 py-4">
            <h4 className="font-size-xl font-weight-bold mb-2">Notificaes</h4>
            {getTotalLabel()}
          </div>
        </div>
        <div className="tabs-animated tabs-animated-shadow tabs-bordered">
          <Nav tabs={true} justified={true}>
            <NavItem>
              <NavLink className={cx({ active: activeTab === 1 })} onClick={toggleTab(1)}>
                <span>Processos</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className={cx({ active: activeTab === 2 })} onClick={toggleTab(2)}>
                <span>Mensagens</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className={cx({ active: activeTab === 3 })} onClick={toggleTab(3)}>
                <span>Tickets</span>
              </NavLink>
            </NavItem>
          </Nav>
        </div>
        <TabContent activeTab={activeTab}>
          <TabPane tabId={1}>{renderPaneItem(globalNotification.process)}</TabPane>
          <TabPane tabId={2}>{renderPaneItem(globalNotification.messages)}</TabPane>
          <TabPane tabId={3}>{renderPaneItem(globalNotification.tickets)}</TabPane>
        </TabContent>
      </UncontrolledPopover>
    </>
  );
};

export default HeaderNotifications;
