import cx from 'clsx';
import React, { FC, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, useLocation } from 'react-router-dom';
import { Dropdown, DropdownMenu, DropdownToggle } from 'reactstrap';
import { HeaderMenu } from 'summer';
import { Reducers } from '../../../store/ducks';
import { GlobalState, referenciaFiscal } from '../../../store/ducks/global.duck';
import './Header.css';
import HeaderCompany from './HeaderCompany';
import HeaderDrawer from './HeaderDrawer';
import HeaderNotifications from './HeaderNotifications';
import HeaderSearch from './HeaderSearch';
import HeaderUserbox from './HeaderUserBox';

export interface HeaderProps {
  sidebarToggleMobile?: boolean;
}

const Header: FC<HeaderProps> = ({ sidebarToggleMobile }) => {
  const { globalParameter, currentState } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [dropdownState, setDropdownState] = useState(false);
  const location = useLocation();

  const toggleDropdown = () => setDropdownState(prev => !prev);

  useEffect(
    () => {
      if (dropdownState) {
        setDropdownState(false);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [location]
  );

  return (
    <>
      <div className="app-header app-header--shadow app-header--opacity-bg">
        <div className="app-header--pane">
          <button className={cx('navbar-toggler hamburger hamburger--elastic toggle-mobile-sidebar-btn', { 'is-active': sidebarToggleMobile })}>
            <span className="hamburger-box">
              <span className="hamburger-inner" />
            </span>
          </button>
          <HeaderSearch />
          <HeaderMenu>
            <Dropdown tag="span" className="position-relative" isOpen={dropdownState} toggle={toggleDropdown}>
              <DropdownToggle color="neutral-dark" size="sm" className="mr-3 btn-transition-none">
                Mdulos
              </DropdownToggle>
              <DropdownMenu className="overflow-hidden border-0 bg-midnight-bloom p-3 mt-2">
                <div className="dropdown-mega-menu-md">
                  <div className="text-center">
                    <div className="font-weight-bold font-size-lg mb-1 text-white">Mdulos</div>
                  </div>
                  <div className="d-flex flex-wrap">
                    <div className="w-50 p-2">
                      <Link to="/ger/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-home h1 d-block my-2 text-dark" />
                          <div className="font-size-md mb-1 text-black-50">Principal</div>
                        </div>
                      </Link>
                    </div>
                    <div className="w-50 p-2">
                      <Link to="/mfp/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-piggy h1 d-block my-2 text-danger" />
                          <div className="font-size-md mb-1 text-black-50">Folha de Pagamento</div>
                        </div>
                      </Link>
                    </div>
                    <div className="w-50 p-2">
                      <Link to="/mcg/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-calculator h1 d-block my-2 text-warning" />
                          <div className="font-size-md mb-1 text-black-50">Contbil</div>
                        </div>
                      </Link>
                    </div>
                    <div className="w-50 p-2">
                      <Link to="/mlf/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-news-paper h1 d-block my-2 text-first" />
                          <div className="font-size-md mb-1 text-black-50">Fiscal</div>
                        </div>
                      </Link>
                    </div>
                    <div className="w-50 p-2">
                      <Link to="/mcp/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-car h1 d-block my-2 text-primary" />
                          <div className="font-size-md mb-1 text-black-50">Controle Patrimonial</div>
                        </div>
                      </Link>
                    </div>
                    <div className="w-50 p-2">
                      <Link to="/mho/dashboard" className="btn card card-box text-left d-flex justify-content-center px-3 py-2 w-100 border-0">
                        <div>
                          <i className="pe-7s-cash h1 d-block my-2 text-success" />
                          <div className="font-size-md mb-1 text-black-50">Honorrios</div>
                        </div>
                      </Link>
                    </div>
                  </div>
                </div>
              </DropdownMenu>
            </Dropdown>
          </HeaderMenu>
        </div>
        <div className="app-header--pane">
          <div className="d-none d-xl-block pl-2" style={{ marginRight: 15, maxWidth: '300px' }}>
            <div className="font-weight-bold" style={{ textOverflow: 'ellipsis', whiteSpace: 'nowrap', overflow: 'hidden' }}>
              {globalParameter && globalParameter.empresa && `${globalParameter.empresa.codigo} - ${globalParameter.empresa.razaoSocial}`}
            </div>
            <span className="text-black-50">{referenciaFiscal(globalParameter, currentState)}</span>
          </div>
          <span className="d-inline-block pr-2">
            <HeaderCompany />
          </span>
          <span className="d-inline-block pr-2">
            <HeaderNotifications />
          </span>
          <HeaderUserbox />
          <HeaderDrawer />
        </div>
      </div>
    </>
  );
};

export default Header;
